import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Pagination } from "swiper/modules";
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import "../css/courses.css";
import FuegoLogo from "../img/FuegoLogoimg.png";


const courseData = [
    {
        id: 1,
        title: "Niv 1",
        image: FuegoLogo, // ndra tillbaka till require() om det behvs
        description: "Fr dig som r helt ny till bachata. Vi gr igenom grundlggande steg, takt och enkla turer.",
        times: "Onsdagar kl 18:00  19:30",
        requirements: "Inga frkunskaper krvs",
        bookingLink: "https://dinbokningssida.se/niva1"
    },
    {
        id: 2,
        title: "Niv 2",
        image: FuegoLogo,
        description: "Vi bygger vidare p grunderna med mer variation, musikalitet och partnering.",
        times: "Mndag kl 18:00  19:30 eller Onsdag kl 19:40  21:10",
        requirements: "Du br ha gtt niv 1 eller motsvarande",
        bookingLink: "https://dinbokningssida.se/niva2"
    },
    {
        id: 3,
        title: "Niv 3 Teknik",
        image: FuegoLogo,
        description: "Teknikkurs med fokus p kroppskontroll, styrning och kontakt.",
        times: "Tisdagar kl 18:00  19:30",
        requirements: "Minst 2 terminer erfarenhet",
        bookingLink: "https://dinbokningssida.se/niva3"
    },
    {
        id: 4,
        title: "Niv 3",
        image: FuegoLogo,
        description: "Teknikkurs med fokus p kroppskontroll, styrning och kontakt.",
        times: "Tisdagar kl 19:40  21:10",
        requirements: "Minst 2 terminer erfarenhet",
        bookingLink: "https://dinbokningssida.se/niva3"
    },
    {
        id: 5,
        title: "Niv 4",
        image: FuegoLogo,
        description: "Avancerad niv med flytande rrelser, footwork och kombinationer.",
        times: "Torsdagar kl 19:40  21:10",
        requirements: "Du har dansat minst 1 r regelbundet",
        bookingLink: "https://dinbokningssida.se/niva4"
    },
    {
        id: 6,
        title: "Niv 4 Teknik",
        image: FuegoLogo,
        description: "Frdjupning i teknik, isoleringar och musikalitet.",
        times: "Torsdagar kl 18:00  19:30",
        requirements: "Niv 4 eller motsvarande erfarenhet",
        bookingLink: "https://dinbokningssida.se/niva4teknik"
    },
    {
        id: 7,
        title: "Niv 5",
        image: FuegoLogo,
        description: "Vr mest avancerade kurs fr dig som vill finslipa stil och performance.",
        times: "Sndagar kl 12:00  14:00",
        requirements: "Inbjudan krvs",
        bookingLink: "https://dinbokningssida.se/niva5"
    }
];

function Courses() {
    return (
        <section className="courses-section">
            <div className="courses-container">
                <h2 className="courses-title">Vra Kurser</h2>
                <Swiper
                    modules={[Navigation, Pagination]}
                    navigation
                    pagination={{ clickable: true }}
                    spaceBetween={20}
                    breakpoints={{
                        0: { slidesPerView: 1 },
                        768: { slidesPerView: 2 },
                        1024: { slidesPerView: 3 }
                    }}
                >
                    {courseData.map(course => (
                        <SwiperSlide key={course.id}>
                            <div className="course-card">
                                <div className="course-image">
                                    <img src={course.image} alt={course.title} />
                                </div>
                                <div className="course-content">
                                    <h3 className="course-title">{course.title}</h3>
                                    <p className="course-description">{course.description}</p>
                                    <div className="course-details">
                                        <p><strong>Tid:</strong> {course.times}</p>
                                        <p><strong>Krav:</strong> {course.requirements}</p>
                                    </div>
                                    <a
                                        href={course.bookingLink}
                                        className="booking-btn"
                                        target="_blank"
                                        rel="noreferrer"
                                    >
                                        Anml dig
                                    </a>
                                </div>
                            </div>
                        </SwiperSlide>

                    ))}
                </Swiper>
            </div>
        </section>
    );
}

export default Courses;
 