import { useState } from 'react';
import "../css/FAQ.css";

const faqData = [
    {
        question: "Jag har aldrig dansat frut  passar era kurser mig?",
        answer: "Absolut! Vra nybrjarkurser r utformade fr dig som aldrig tagit ett danssteg tidigare. Vi guidar dig steg fr steg."
    },
    {
        question: "Behver jag komma med en partner?",
        answer: "Nej! Du kan komma sjlv  vi roterar partners i klassen s att alla fr dansa med alla."
    },
    {
        question: "Vad r Bachata Sensual?",
        answer: "Bachata Sensual r en modern stil av bachata med mjuka, flytande rrelser och starkt fokus p kontakt och musikalitet."
    },
    {
        question: "Vad ska jag ha p mig?",
        answer: "Bekvma klder du kan rra dig fritt i. Sneakers eller dansskor med glid fungerar bra  inget krav p speciella skor."
    },
    {
        question: "Hur lng r varje kurs?",
        answer: "Vra kurser r 8 veckor lnga med 1 lektion i veckan som r 1,5 timma lnga."
    },
    {
        question: "Hur anmler jag mig?",
        answer: "Via knappen p hemsidan! Fyll i formulret s skickar vi bekrftelse inom 24 timmar."
    },
    {
        question: "Har ni drop-in kurser?",
        answer: "Nej tyvrr. Vi har endast kurser som r 8 veckor lnga. Du kan dock alltid prova p frsta lektionen gratis"
    },
    {
        question: "Jag r r student  fr jag rabatt?",
        answer: "Ja! Vi erbjuder 15% rabatt p alla kurser fr studenter. Glm inte att ta med studentlegitimation."
    },
    {
        question: "Jag kan bara komma varannan vecka  kan jag nd anmla mig?",
        answer: "Ja! Om man kan bara komma varannan vecka fr man 35% rabatt p kursavgiften."
    }
];

function FAQ({ visible, onClose }) {
    const [activeIndex, setActiveIndex] = useState(null);

    if (!visible) return null;

    const toggleAnswer = (index) => {
        setActiveIndex(activeIndex === index ? null : index);
    };

    const handleClose = () => {
        console.log("Modal stngs nu");
        onClose();
    };

    return (
        <div className="faq-overlay" onClick={handleClose}>
            <div className="faq-modal" onClick={(e) => e.stopPropagation()}>
                <button className="faq-close" onClick={handleClose}></button>
                <h2 className="faq-title">Vanliga frgor</h2>
                <div className="faq-list">
                    {faqData.map((item, index) => (
                        <div key={index} className="faq-item">
                            <div
                                className={`faq-question ${activeIndex === index ? "active" : ""}`}
                                onClick={() => toggleAnswer(index)}
                            >
                                {item.question}
                            </div>
                            <div className={`faq-answer ${activeIndex === index ? "show" : ""}`}>
                                {item.answer}
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );
}

export default FAQ;
